.App {
    text-align: center;
}

.App-logo {
    pointer-events: none;
    height: 40vmin;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    min-height: 100vh;

    font-size: calc(10px + 2vmin);
    color: white;

    background-color: #282c34;
}

.App-link {
    color: #61dafb;
}

.linearProgress {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
}

